.nav {
  list-style: none;
  padding: 0;
}

.nav li {
  display: inline-block;
  margin-right: 20px;
}

.dropdown {
  position: relative;
  display: inline-block;
  cursor: pointer;
  top: 22px;
  font-size: 18px;
}

.dropdown:hover .dropdown-content {
  display: block;
}

.dropdown .dropdown-content {
  display: none;
  position: absolute;
  background: transparent;
  /* Transparent background */
  min-width: 160px;
  max-width: 200px; /* Set a maximum width for the dropdown content */
  border-radius: 5px;
  z-index: 1;
  white-space: nowrap;
  /* Prevent items from going to the next line */
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  /* Add a box shadow for a fancy look */
  left: 0;
  /* Align the dropdown to the left */
}

.dropdown-content {
  list-style: none;
  padding: 0;
  text-align: left;
  /* Align the text to the left */
}

.dropdown-content li {
  display: block;
  /* Display items in a block */
  padding: 10px;
  text-decoration: none;
  color: #333;
  transition: 0.3s;
  background: #f2f2f2;
  /* Background color for each item */
  border-radius: 5px;
  margin: 5px;
}

.dropdown-content li:hover {
  color: #007bff;
  /* Change text color on hover */
  background: #007bff;
  /* Background color on hover */
  transition: 0.3s;
  border-radius: 5px;
}
